








import {Component,Vue} from 'vue-property-decorator'
import {Form} from '@/pages/adminWeb/components/views/user/login/index.ts'
// import {Form} from '../../components/views/user/login/index.ts'
import {isEmpty} from '@/utils'

@Component({
  components:{Form,}
})
export default class ViewComponent extends Vue {
  /* data */
  //- 表单模块


  /* method */
  // 监听
  // 监听:表单模块
  emitForm(response:any){
    // 点击“注册”按钮
    const data = response.data
    this.request({
      api:'adminLogin',
      params:data,
      method:'post'
    })
  }
  // 执行方法
  async request(conf:any){
    const data = await this.$request(conf)
    if(data.resCode === '0000'){
      this.$store.commit('setToken',data.token)
      this.$router.push ('/dashboard');

      const token = data.token;
      const response = await this.$request({
        direct:'api/admin/3.0/W/'+'privileges',
        params:{
          token,
        }
      })
      if(response.resCode === '0000'){
        delete response.resCode;
        delete response.resMsg;
        this.$store.commit('setUser',response)
      }
    }
  }
}
