import CryptoJS from 'crypto-js'
//解密方法
function Decrypt(word) {
  const key = CryptoJS.enc.Utf8.parse("qgE9nDcorHvK72yn");  //十六位十六进制数作为密钥
  const iv = CryptoJS.enc.Utf8.parse('16-Bytes--String');
  let decrypt = CryptoJS.AES.decrypt(word, key, { iv: iv, mode: CryptoJS.mode.CBC, padding: CryptoJS.pad.Pkcs7 });
  let decryptedStr = decrypt.toString(CryptoJS.enc.Utf8);
  return decryptedStr.toString();
}

//加密方法
function Encrypt(word) {
  const key = CryptoJS.enc.Utf8.parse("qgE9nDcorHvK72yn");  //十六位十六进制数作为密钥
  const iv = CryptoJS.enc.Utf8.parse('16-Bytes--String');
  let srcs = CryptoJS.enc.Utf8.parse(word);
  let encrypted = CryptoJS.AES.encrypt(srcs, key, {iv:iv,mode: CryptoJS.mode.CBC, padding: CryptoJS.pad.Pkcs7 });
  return CryptoJS.enc.Base64.stringify(encrypted.ciphertext)
}

export {
  Encrypt,
  Decrypt
}
